const date = new Date();
let hours = date.getHours();
let salut = (hours < 12) ? "Good Morning" : ((hours <= 20 && hours <= 18 && hours >= 12 )
 ? "Good Afternoon" : "Good Evening" : "Good Night");


const Ekene= () => {
  return (
    <h6 className='text-center font-weight-bold mt-3'>{salut} Kekesmovic</h6>
  )
}

export default Ekene;
